<template>
    <div>
    <img :src="imgUrl" alt="海报">
    <div id="my"><i id="icon" class="el-icon-user" @click="handleGoCenter"></i></div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  methods: {
    handleGoCenter () {
      this.$router.push('/center')
    }
  },
  data () {
    return {
      navPoster: []
    }
  },
  computed: {
    imgUrl () {
      return this.navPoster[0] && this.navPoster[0].imgUrl
    }
  },
  mounted () {
    axios({
      url: 'https://m.maizuo.com/gateway?type=2&cityId=310100&k=4136129',
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1601188733181586922307585","bc":"310100"}',
        'X-Host': 'mall.cfg.common-banner'
      }
    }).then(res => { this.navPoster = res.data.data; console.log(this.navPoster, res.data) })
  }

}
</script>

<style lang="scss" scoped>
img{
    width: 100%;
}
#my{
  width: 40px;
  height: 40px;
  border-radius: 20px;
  z-index: 999;
  right: 30px;
  top: 50px;
  position: absolute;
  font-size: 30px;
  background-color: blanchedalmond;
  text-align: center;
}
</style>
