<template>
    <div >
     <!-- 声明式导航
    <router-link to="/detail" tag="div" v-for="data in datalist" :key="data.filmId">
        <img :src="data.poster"/>
        <h3>{{data.name}}</h3>
        <p>观众评分{{data.grade}}</p>
    </router-link> -->
    <div id="total">
    <div id="filmPic" v-for="data in datalist" :key="data.filmId" @click="handleChangePage(data.filmId)">
        <img :src="data.poster"/>
        <h3>{{data.name}}</h3>
        <p>观众评分{{data.grade}}</p>
    </div>
    </div>
<div id="pageIndex">
     <div class="block">
  <!-- <span class="demonstration"></span> -->
  <el-pagination
    @current-change="handleChangePageIndex"
    layout="prev, pager, next"
    :total="this.total">
  </el-pagination>
</div>
</div>

    <div id="foot">
      <h3>3</h3>
    </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      datalist: [],
      total: null,
      count: 1
    }
  },
  methods: {
    handleChangePageIndex (pager) {
      axios({
        url: `https://m.maizuo.com/gateway?cityId=310100&pageNum=${pager}&pageSize=10&type=1&k=7422532`,
        headers: {
          'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1601188733181586922307585","bc":"310100"}',
          'X-Host': 'mall.film-ticket.film.list'
        }
      }).then(res => {
        this.datalist = res.data.data.films
      })
      this.$router.push('/film')
    },
    handleChangePage (myid) {
      this.$router.push(`/detail/${myid}`)
    }
  },
  mounted () {
    axios({
      url: `https://m.maizuo.com/gateway?cityId=310100&pageNum=${this.count}&pageSize=10&type=1&k=7422532`,
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1601188733181586922307585","bc":"310100"}',
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then(res => {
      console.log(res.data)
      this.datalist = res.data.data.films
      this.total = res.data.data.total
    })
  }
}
</script>

<style lang="scss" scoped>

#total {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
#filmPic{
  width: 229px;
  // flex: 1;
  margin: 20px;
  img{
    width: 200px;
    height: 300px;
  }
}
#pageIndex{
  text-align: center;
}

#foot{
  width: 100%;
  height: 50px;
  bottom: 0px;
  display: block;
}
}
.block{
  text-align: center;
}
</style>
