<template>
<div>
<el-container>
  <el-header id="header">个人中心</el-header>
  <el-container id="container">
    <el-aside width="200px" id="aside">
      <div id="goMyOrder" @click="goMyOrder">我的订单</div>
      <div id="goMyDetail" @click="goMyDetail">基本信息</div>
    </el-aside>
    <el-main id="main">
      <router-view></router-view>
    </el-main>
  </el-container>
</el-container>
<return></return>
</div>
</template>
<script>
import Return from '@/views/Return'
import bus from '@/views/bus/bus.js'
// import axios from 'axios'
export default {
  beforeRouteEnter (to, from, next) {
    console.log('局部盘查')
    if (localStorage.getItem('const')) {
      next()
    } else {
      next('/login')
    }
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
  },
  data () {
    return {
      const: null
    }
  },
  mounted () {
    this.const = localStorage.getItem('const')
  },
  components: { Return },
  methods: {
    goMyOrder () {
      this.$router.push('/center/myorder')
    },
    goMyDetail () {
      this.$router.push('/center/mydetail')
    }
  },
  beforeMount () {
    bus.$emit('maizuo', false)
    this.$store.commit('HideNavBar', false)
    this.$store.commit('HideCity0', false)
  },
  // beforeUpdate () {
  //   bus.$emit('maizuo', false)
  //   this.$store.commit('HideNavBar', false)
  //   this.$store.commit('HideCity0', false)
  // },
  destroyed () {
    bus.$emit('maizuo', true)
    this.$store.commit('ShowNavBar', true)
    this.$store.commit('HideCity0', true)
  }
}
</script>
<style lang="scss" scoped>
#header{
  text-align: center;
  font-size: 20px;
  line-height: 55px;
  background-color: rgb(231, 234, 241);
}
  #container{
    #aside{
      background-color: #fff;
      #goMyOrder{
       height: 50px;
       text-align: center;
       line-height: 50px;
       background-color:  rgb(216, 187, 187);
      }
      #goMyDetail{
       height: 50px;
       text-align: center;
       line-height: 50px;
       background-color:darkgray;
      }
    }
    #main{
      background-color: cadetblue;
    }
  }

</style>
