import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    amShow: true,
    comingSoon: null,
    city: [],
    cityName: null,
    myconst: false,
    myconst2: false,
    hideCity: true,
    id: localStorage.getItem('cityId')
  },
  mutations: {
    HideCity0 (state, data) {
      state.hideCity = data
    },
    ChangeMyconst2 (state, data) {
      state.myconst2 = data
    },
    ChangeMyconst (state) {
      state.myconst = !state.myconst
    },
    ChangeCityName (state, data) {
      state.cityName = data
    },
    HideNavBar (state, data) {
      state.amShow = data
    },
    ShowNavBar (state, data) {
      state.amShow = data
    },
    amComingSoon (state, data) {
      state.comingSoon = data
    }
  },
  actions: {
    getComingSoon (store) {
      axios({
        url: `https://m.maizuo.com/gateway?cityId=${store.state.id}&pageNum=1&pageSize=10&type=2&k=9916177`,
        headers: {
          'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1601188733181586922307585","bc":"310100"}',
          'X-Host': 'mall.film-ticket.film.list'
        }
      }).then(res => {
        console.log(res.data, 'dadioawjdoajw')
        store.commit('amComingSoon', res.data.data)
      })
    },
    ChangePageIndex (page) {
      axios({
        url: `https://m.maizuo.com/gateway?cityId=310100&pageNum=${page}&pageSize=10&type=2&k=9916177`,
        headers: {
          'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1601188733181586922307585","bc":"310100"}',
          'X-Host': 'mall.film-ticket.film.list'
        }
      }).then(res => {
        console.log(res.data, 'dadioawjdoajw')
        this.$store.commit('amComingSoon', res.data.data)
      })
    }
  },
  modules: {
  }
})
