<template  v-if="this.datalist1.length">
    <div>
    我是detail
    <swiper id="swiper">
      <swiper-slide v-for="(data,index) in datalist1.photos" :key="index"><img :src="data" alt="poster"></swiper-slide>
    <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
    {{data}}
    </div>
</template>

<script>
import Swiper from './Swiper.vue'
import axios from 'axios'
import bus from '@/views/bus/bus.js'
export default {
  data () {
    return {
      datalist1: null
    }
  },
  // props: ['myid'],
  components: {
    Swiper
  },
  beforeMount () {
    bus.$emit('maizuo', false)
    this.$store.commit('HideNavBar', false)
  },
  destroyed () {
    bus.$emit('maizuo', true)
    this.$store.commit('ShowNavBar', true)
  },
  mounted () {
    console.log(this.$route.params.myid, this.myid)
    axios({
      url: `https://m.maizuo.com/gateway?filmId=${this.$route.params.myid}&k=4281592`,
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1601188733181586922307585","bc":"310100"}',
        'X-Host': 'mall.film-ticket.film.info'
      }
    }).then(res => {
      console.log(res.data)
      this.datalist1 = res.data.data.film
    })
  }
}
</script>

<style lang="scss" scoped>
#swiper{
  width: 100%;
    height: 600px;
}
img{
    width: 100%;
    height: 600px;
}
</style>
