<template>
 <div id="total" @mouseenter="handleShowCity" @mouseleave="handleHideCity">
   <div id="cityname">{{this.$store.state.cityName}}</div>
   <div v-show="cityShow" class="CityShow">
       <div class="ABC" v-for="data in this.cityAAA" :key="data.index">
         {{data.index}}
         <div><div id="ABCinline" v-for="city in data.list" :key="city.cityId" @click="handleBindCity(city.cityId,city.name)">{{city.name}}</div></div>
       </div>
   </div>
 </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      dataCity: [],
      cityShow: false,
      cityAAA: [],
      cityName: null
    }
  },
  methods: {
    handleBindCity (id, name) {
      localStorage.setItem('cityId', id)
      localStorage.setItem('cityName', name)
      this.$store.commit('ChangeCityName', name)
      if (this.$store.state.myconst) {
        this.$store.commit('HideNavBar', false)
        // this.$router.push('/cinema')
        setTimeout(() => {
          this.$store.commit('ChangeMyconst2', true)
          this.$router.go(-1)
        }, 100)
        // this.$router.push('/cinema')
        // setTimeout(() => {
        //   this.$router.go(1)
        // }, 100)
        // this.$router.push({
        //   path: '/cinema',
        //   query: {
        //     t: Date.now
        //   }
        // })
        this.$store.commit('HideNavBar', false)
      } else {
        this.$router.push('/film')
        // this.$router.push({
        //   path: '/film',
        //   query: {
        //     t: Date.now
        //   }
        // })
      }
    },
    handleShowCity () {
      this.cityShow = true
    },
    handleHideCity () {
      this.cityShow = false
    }
  },
  mounted () {
    axios({
      url: 'https://m.maizuo.com/gateway?k=7772695',
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1601188733181586922307585","bc":"310100"}',
        'X-Host': 'mall.film-ticket.city.list'
      }
    }).then(res => {
      // console.log(res.data)
      this.dataCity = res.data.data.cities
      console.log(this.dataCity)
      const abc = []
      for (let i = 65; i < 91; i++) {
        abc.push(String.fromCharCode(i))
      }
      const newabc = []
      for (let j = 0; j < abc.length; j++) {
        const arr = this.dataCity.filter(item => item.pinyin.substring(0, 1) === abc[j].toLowerCase())
        // console.log(arr)
        if (arr.length > 0) {
          newabc.push({
            index: abc[j],
            list: arr
          })
        }
      }
      console.log(newabc, 'woshicityc')
      this.cityAAA = newabc
      console.log(this.cityAAA, 'woshicityc1')
      this.$store.commit('ChangeCityName', localStorage.getItem('cityName'))
    })
  }
}
</script>

<style lang="scss" scoped>
#total{

  #cityname{
    position: fixed;
    left: 23px;
    top: 45px;
    z-index: 999;
    background-color: rgb(241, 241, 241);
    border-radius: 8px;
    opacity: 60%;
}
 .CityShow{
    //  display: flex;
    //  flex-direction:row;
    //  flex-wrap: wrap;
     opacity: 98%;
     width: 500px;
     height: 450px;
     background-color:whitesmoke;
     position: fixed;
     left: 24px;
     top: 65px;
     z-index: 999;
     overflow: auto;
    .ABC{
      word-break: break-all;
      word-wrap: break-word;
      #ABCinline{
        display: inline;
        margin-right: 5px;
      }
    }
 }
}

</style>
