<template>
  <div>
    <return id="return"></return>
    <div id="Cinema">影院</div>
    <search></search>
    <div id="heads"></div>
    <div id="search" @click="handleSearch"><i class="el-icon-search"></i></div>
    <div id="list" v-for="data in dataCinema" :key="data.cinemaId">{{ data.name }}<p>{{ data.address }}</p></div>
  </div>
</template>

<script>
import Return from '@/views/Return'
import axios from 'axios'
import bus from '@/views/bus/bus.js'
import Search from '@/views/Search.vue'
export default {
  components: {
    Return,
    Search
  },
  data () {
    return {
      dataCinema: []
    }
  },
  computed: {
  },
  methods: {
    handleSearch () {
    }
  },
  mounted () {
    const id = localStorage.getItem('cityId')
    axios({
      url: `https://m.maizuo.com/gateway?cityId=${id}&ticketFlag=1&k=5313079`,
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1601188733181586922307585","bc":"310100"}',
        'X-Host': 'mall.film-ticket.cinema.list'
      }
    }).then(res => {
      // console.log(res.data)
      this.dataCinema = res.data.data.cinemas
    })
  },
  beforeMount () {
    bus.$emit('maizuo', false)
    this.$store.commit('HideNavBar', false)
    this.$store.commit('ChangeMyconst')
  },
  destroyed () {
    bus.$emit('maizuo', true)
    this.$store.commit('ShowNavBar', true)
    this.$store.commit('ChangeMyconst')
    if (this.$store.state.myconst2) {
      this.$store.commit('ChangeMyconst2', false)
      this.$router.push('/cinema')
    }
  }
}
</script>

<style lang="scss" scoped >
html{
  margin: 0px;
  padding: 0px;
}
body{
  margin: 0px;
  padding: 0px;
}
#Cinema{
  width: 100%;
  height: 80px;
  text-align: center;
  line-height: 80px;
  font-size: 40px;
  position: fixed;
  background-color: white;
  border-bottom-style: solid ;
  background-color: whitesmoke;
  border-color: rgb(202, 199, 194);
}
#heads{
  width: 100%;
  height: 82px;
}
#list{
  font-size: 18px;
  width: 100%;
  border: 1px solid;
  border-color:rgb(223, 216, 216);
  p{
    font-size: 10px;
  }
}
#search{
  right: 24px;
  top: 20px;
  width: 15px;
  height: 15px;
  opacity: 80%;
  position: fixed;
  text-align: center;
  font-size: 20px;
  z-index: 99;
}

</style>
