<template>
    <div>
    </div>
</template>

<script>
import bus from '@/views/bus/bus.js'
import axios from 'axios'
export default {
  data () {
    return {
      dataCity: []
    }
  },
  beforeMount () {
    bus.$emit('maizuo', false)
    this.$store.commit('HideNavBar', false)
  },
  destroyed () {
    bus.$emit('maizuo', true)
    this.$store.commit('ShowNavBar', true)
  },
  mounted () {
    axios({
      url: 'https://m.maizuo.com/gateway?k=7772695',
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1601188733181586922307585","bc":"310100"}',
        'X-Host': 'mall.film-ticket.city.list'
      }
    }).then(res => {
      // console.log(res.data)
      this.dataCity = res.data.data.cities
      console.log(this.dataCity)
    })
  }
}
</script>

