<template>
<div>
<el-container>
  <el-header id="header">基本信息</el-header>
  <el-container>
    <el-aside id="aside" width="400px">
        <div id="amimg">
            <img src="https://img.meituan.net/avatar/a4e808ca246c80762dc8718b3bccd29339917.jpg" alt="">
        </div>
        <div  id="ambutton">
            <el-button round>更换头像</el-button>
        </div>
    </el-aside>
    <el-main>
        <div>
            <el-row class="row">
                <el-col :span="3" id="text">
                   昵称：
                </el-col>
                <el-col :span="8">
            <el-input v-model="input" placeholder="请输入内容"></el-input>
                </el-col>
            </el-row>

            <el-row class="row">
                <el-col :span="3" id="text">
                   性别：
                </el-col>
                <el-col :span="8" class="lineCenter">
             <el-radio v-model="radioSex" label="1">男</el-radio>
             <el-radio v-model="radioSex" label="2">女</el-radio>
                </el-col>
            </el-row>

            <el-row class="row">
                <el-col :span="3" id="text">
                   生日：
                </el-col>

                <div class="block">
                    <span class="demonstration"></span>
                    <el-date-picker
                    v-model="valueBirth"
                    type="date"
                    placeholder="选择日期">
                    </el-date-picker>
                </div>
            </el-row>

            <el-row class="row">
                <el-col :span="3" id="text">
                   生活状态：
                </el-col>
                <el-col :span="16">
                        <el-radio v-model="lifeStyle" label="1"  class="lineCenter">单身</el-radio>
                        <el-radio v-model="lifeStyle" label="2" class="lineCenter">热恋中</el-radio>
                        <el-radio v-model="lifeStyle" label="3" class="lineCenter">已婚</el-radio>
                        <el-radio v-model="lifeStyle" label="4" class="lineCenter">为人父母</el-radio>
                </el-col>
            </el-row>

            <el-row class="row">
                <el-col :span="3" id="text">
                   兴趣：
                </el-col>
                <el-col :span="16">
                        <!-- <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox> -->
                        <div style="margin: 15px 0;"></div>
                        <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                            <el-checkbox v-for="city in cities" :label="city" :key="city" >{{city}}</el-checkbox>
                        </el-checkbox-group>
                </el-col>
            </el-row>

            <el-row class="row">
                <el-col :span="3" id="text">
                   个性签名：
                </el-col>
                <el-col :span="8">
            <el-input v-model="input" placeholder="请输入内容" class="row1"></el-input>
                </el-col>
            </el-row>

            <el-row class="row">
                <el-col :span="3" :offset="8" >
                <el-button round id="baocun" @click="handleSave">保存</el-button>
                </el-col>
            </el-row>
        </div>
    </el-main>
  </el-container>
</el-container>
</div>
</template>

<script>
// import axios from 'axios'
const cityOptions = ['美食', '动漫', '摄影', '电影', '体育', '财经', '音乐', '游戏', '科技', '旅游', '文学', '公益', '汽车', '时尚', '宠物']
export default {
  data () {
    return {
      inputName: '',
      radioSex: '1',
      pickerOptions: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [{
          text: '今天',
          onClick (picker) {
            picker.$emit('pick', new Date())
          }
        }, {
          text: '昨天',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', date)
          }
        }, {
          text: '一周前',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', date)
          }
        }]
      },
      valueBirth: '',
      lifeStyle: 1,
      checkAll: false,
      checkedCities: [],
      cities: cityOptions,
      isIndeterminate: true
    }
  },
  methods: {
    handleCheckAllChange (val) {
      this.checkedCities = val ? cityOptions : []
      this.isIndeterminate = false
    }
  },
  handleCheckedCitiesChange (value) {
    const checkedCount = value.length
    this.checkAll = checkedCount === this.cities.length
    this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length
  },
  handleSave () {

    // 丢到action去处理异步
    // axios({
    //   method: 'post',
    //   url: '/user/12345',
    //   data: {
    //     inputName: this.inputName,
    //     radioSex: this.radioSex,
    //     valueBirth: this.valueBirth,
    //     lifeStyle: this.lifeStyle
    //   }
    // }).then(res => {
    //   console.log('s')
    // })
  }
}
</script>

