#aside{
    // display: flex;
    // flex-flow: row wrap;
    // justify-content: center;
    #amimg{
        width: 100%;
        text-align: center;
        margin: 20px 0px 0 0;
       img{
           width: 280px;
           height: 280px;
       }
    }
    #ambutton{
         margin: 20px 0px;
         width: 100%;
         text-align: center;
    }
}

#forms{
    padding: 0;
    margin: 0;
}

#text{
    text-align: right;
    line-height: 40px;
}

.row{
    margin: 0px 0 5px 0;
}

.row1{
    margin: 5px 0 0 0;
}

